<template>
  <div>
    <!--물온도 박스-->
    <div id="temp_box" class="box">
      <div class="box_title">물 온도</div>
    </div>
    <!--높이 조절-->

    <!--냉수 밸브-->
    <!--온수 밸브-->
    <div class="boxcontainer">
      <div id="waterlevel_box" class="box half">
        <div class="box_title">높이 조절</div>
      </div>
      <div class="half">
        <button id="cvalve_box" class="box half_top" v-on:click="btnColdValve">
          <div class="box_title">
            {{ bathState["c_valve"] ? "냉수 밸브 닫기" : "냉수 밸브 열기" }}
          </div>
        </button>
        <button id="hvalve_box" class="box" v-on:click="btnHotValve">
          <div class="box_title">
            {{ bathState["h_valve"] ? "온수 밸브 닫기" : "온수 밸브 열기" }}
          </div>
        </button>
      </div>
    </div>
    <!--욕조 청소-->
    <!--마개 버튼-->
    <div class="boxcontainer">
      <button id="clean_box" class="box half" v-on:click="btnBathClean">
        <div class="box_title">욕조 청소</div>
        <img src="../../src/assets/img/clean.png" alt="" />
      </button>
      <div class="half">
        <button id="cap_box" class="box half_top" v-on:click="btnCap">
          <div class="box_title">
            {{ bathState["cap"] ? "마개 닫기" : "마개 열기" }}
          </div>
          <img src="../../src/assets/img/cap_close.png" alt="" />
        </button>
        <div id="LED_box" class="box">
          <div class="box_title">LED</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast } from "vue-toastification";

export default {
  setup() {
    // Get toast interface
    const toast = useToast();
    /*
    toast("I'm a toast!");
    toast.success("My toast content", {
      timeout: 2000,
    });
    // These options will override the options defined in the "app.use" plugin registration for this specific toast
    */
    // Make it available inside methods

    return { toast };
  },
  data() {
    return {
      coldValvetext: "냉수 밸브 열기",
      hotValvetext: "온수 밸브 열기",
      captext: "마개 열림",
    };
  },
  props: {
    bathState: {
      type: Object,
      default: () => {
        return {};
      },
    },
    userArray: {
      type: Array,
      default: undefined,
    },
  },
  methods: {
    //냉수 밸브 열기/닫기
    btnColdValve() {
      var orderc_valve = 0;
      var toastmsg = "냉수 밸브를 닫습니다.";

      if (this.bathState.c_valve == 0) {
        orderc_valve = 1;
        toastmsg = "냉수 밸브를 엽니다.";
      }

      this.$http
        .post("control/1", {
          cap: this.bathState.cap,
          h_valve: this.bathState.h_valve,
          c_valve: orderc_valve,
          cleantime: this.bathState.clean_valve,
          user_id: 1,
          temp: 0,
          level: 0,
        })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.error(err);
        });
      this.toast(toastmsg);
    },
    //온수 밸브 열기/닫기
    btnHotValve() {
      var orderh_valve = 0;
      var toastmsg = "온수 밸브를 닫습니다.";

      if (this.bathState.h_valve == 0) {
        orderh_valve = 1;
        toastmsg = "온수 밸브를 엽니다.";
      }

      this.$http
        .post("control/1", {
          cap: this.bathState.cap,
          h_valve: orderh_valve,
          c_valve: this.bathState.c_valve,
          cleantime: this.bathState.clean_valve,
          user_id: 1,
          temp: 0,
          level: 0,
        })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.error(err);
        });
      this.toast(toastmsg);
    },
    //목욕 명령
    btnBathClean() {
      this.$http
        .post("control/1", {
          cap: this.bathState.cap,
          h_valve: this.bathState.h_valve,
          c_valve: this.bathState.c_valve,
          clean: 15,
          user_id: 1,
          temp: 0,
          level: 0,
        })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.error(err);
        });
      this.toast("욕조 청소가 시작됩니다.");
    },
    //마개 열기 닫기
    btnCap() {
      var ordercap = 0;
      var toastmsg = "마개를 닫습니다.";

      if (this.bathState.cap == 0) {
        ordercap = 1;
        toastmsg = "마개를 엽니다.";
      }

      this.$http
        .post("control/1", {
          cap: ordercap,
          h_valve: this.bathState.h_valve,
          c_valve: this.bathState.c_valve,
          cleantime: this.bathState.clean_valve,
          user_id: 1,
          temp: 0,
          level: 0,
        })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.error(err);
        });
      this.toast(toastmsg);
    },
  },
};
</script>
<style scoped>
#temp_box {
  background-color: white;
  border-radius: 10px;
  margin: 10px 0px;
  box-shadow: 1px 1px 0px 0px;
}
.box {
  background-color: white;
  text-align: start;
  border: white;
  border-radius: 10px;
  box-shadow: 1px 1px 0px 0px;
  width: 100%;
}
.boxcontainer {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  align-items: stretch;
  margin: 10px 0px;
}
.half {
  width: 49%;
}
.half_top {
  margin-bottom: 10px;
}
.box_title {
  padding: 10px;
  font-weight: bold;
}
</style>