<template>
  <div>
    <table>
      <thead>
        <tr>
          <th v-for="header in headers" :key="header.text">
            {{ header.text }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in items" :key="index">
          <td>
            {{ item["user_id"] }}
          </td>
          <td>{{ item["start_time"] }} ~ {{ item["end_time"] }}</td>
          <td>
            {{ item["bath_time"] }}
          </td>
          <td>
            {{ item["temp"] }}
          </td>
          <td>
            {{ item["level"] }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
export default {
  props: {
    items: Array,
    headers: {
      type: Array,
      required: true,
    },
  },
};
</script>